.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

.header {
  flex-basis: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
  box-shadow: 0 0 5px #ddd;
}


.input-wrapper {
  box-shadow: 0px 0px 5px #ddd;
  overflow: hidden;
  padding: 3px 15px;
  border-radius: 10px;
}

.input {
  border: none;
  width: 230px;
  height: 26px;
  font-size: 13px;
  color: #666;
  outline: none;
}

.container {
  flex-grow: 1;
}